.card {
  margin: 20px;
  width: 18rem;
  background-color: rgba(255, 251, 251, 0.1);
  font-family: 'Orelega One', cursive;
  color: aliceblue;
  text-shadow: 1px 1px 3px black;
  border: 2px solid rgba(255, 255, 255, 0.199);
  border-radius: 1em;
  box-shadow: 2px 2px 10px white inset;
  backdrop-filter: blur(15px);
}

.card-title {
  margin-bottom: 15px;
  font-size: 1.5rem;
}

#closeIcon {
  display: flex;
  justify-content: flex-end;
  margin-right: 8px;
  margin-top: 8px;
  
}
.close-button{
  border-radius: 100%;
}

.card-body {
  margin-top: 0px !important;
  padding-top: 0px !important;

}
.card p{
  font-size: 1.2rem;
}
